import React, { useState } from 'react';
import { login } from '../components/utils/isLogin';
import '../assets/styles/login.css';
import axios from 'axios';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import config from './../config';

toast.configure()
const Login = (props) => {

    const [password, setPassword] = useState('')

    const handleLogin = () => {
        axios.post(`${config.host}api/login-bodega`, {
            password,
        }).then(function (response) {
            console.log(response.data);
            if (response.data.token) {
                login(response.data.token);
                props.history.push('/request-product');
            } else {
                toast.error(response.data.message)
            }
        }).catch(function (error) {
            console.log(error);
        });
    }

    const onChangePassword = e => {
        setPassword(e.target.value)
    }

    return (
        <div className='login-wrapper'>
            <div className='login'>
                <div className='header'>INICIAR SESION</div>
                <div className='body'>
                    <input type='text' value={password} onChange={onChangePassword} />
                </div>
                <div className='footer'>
                    <button onClick={() => handleLogin()} >INGRESAR</button>
                </div>

            </div>
        </div>
    );
};

export default Login;