
import React, { useEffect, useState } from 'react';
import '../assets/styles/request-products.css';
import axios from 'axios';
import config from './../config';

import { socket, connect } from '../components/utils/Socket'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Request from './Request';
toast.configure()
var voces = []
const RequestProduct = props => {

    const [requests, setRequests] = useState([])

    const initSpeech = () => {
        speechSynthesis.getVoices();
        setTimeout(function () {
            // After 1 second, get the voices now Chrome is listening.
            voces = speechSynthesis.getVoices()
            speechSynthesis.getVoices().forEach(function (_) {
                // console.log('Hi! My name is ', voice.name);
            });
        }, 1000);
    }
    useEffect(() => {

        initSpeech()
        connect().then(() => {

            socket.on('enviarPedidoBodega', data => {

                var index = speechSynthesis.getVoices().findIndex(function (voice) {
                    return voice.name === 'Paulina';
                });

                var utterance = new SpeechSynthesisUtterance();
                utterance.text = data.nombre_voz;
                utterance.voice = voces[index]
                speechSynthesis.speak(utterance);

                let diff = Math.abs(new Date(data.date) - new Date());
                let minutes = Math.floor((diff / 1000) / 60);
                data.minutes = minutes;
                setRequests(requests => [...requests, data])
            });

            axios.post(`${config.host}api/obtenerSolicitudes`, {
            }).then(function (response) {
                if (response.data.status) {
                    setRequests(response.data.requests)
                }
            }).catch((error) => {
                console.log(error);
            });
        });
    }, [])


    return (
        <div className="request-products">
            <div className="request-products-header">
                <div className="request-products-header-name"></div>
                <div className="request-products-header-quantity">{requests.length}</div>
            </div>
            <div className="request-products-content">
                {
                    requests.map((request, index) => {
                        return (<Request key={index} request={request} />)

                    })
                }
            </div>
        </div>
    );
}
export default RequestProduct