
import React, { useEffect, useState } from 'react';
import '../assets/styles/request-products.css';
import Eye from '../assets/images/eye.svg';
import Printer from '../assets/images/printer.svg';
import { Link } from 'react-router-dom';
import axios from 'axios';
import config from './../config';

import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
toast.configure()


const LIMIT_ITEM_CARD = 10

const Request = props => {

    const [counter, setCounter] = useState(0);
    const [request, setRequest] = useState(props.request);

    useEffect(() => {
        const interval = setInterval(() => {
            setCounter(counter + 1);
            let diff = Math.abs(new Date(request.date) - new Date());

            setRequest({ ...request, minutes: getYoutubeLikeToDisplay(diff) })
        }, 1000);

        return () => {
            clearInterval(interval);
        };
    });

    const onPrintOrder = () => {
        console.log(request.id)


        axios.post(`${config.urlPrinter}print`, {
            request: request,
        }).then(function (response) {
            console.log('impresoe', response)
        }).catch((error) => {
            console.log(error);
        });
    }

    const getYoutubeLikeToDisplay = millisec => {
        var seconds = (millisec / 1000).toFixed(0);
        var minutes = Math.floor(seconds / 60);
        var hours = "";
        if (minutes > 59) {
            hours = Math.floor(minutes / 60);
            hours = (hours >= 10) ? hours : "0" + hours;
            minutes = minutes - (hours * 60);
            minutes = (minutes >= 10) ? minutes : "0" + minutes;
        }

        seconds = Math.floor(seconds % 60);
        seconds = (seconds >= 10) ? seconds : "0" + seconds;
        if (hours != "") {
            return hours + ":" + minutes + ":" + seconds;
        }
        return minutes + ":" + seconds;
    }



    return (
        <div className='card' >
            <div className={`username ${request.minutes <= 15 ? 'green' : request.minutes > 15 && request.minutes <= 30 ? 'orange' : 'red'}`}>{request.to}</div>
            <div className='content'>
                {
                    request.products.map((product, index) => {
                        if (index >= (LIMIT_ITEM_CARD + 2))
                            return null
                        if (index <= LIMIT_ITEM_CARD) {
                            return (
                                <p key={index} className={product.status}>{product.nombre}</p>
                            )
                        } else {
                            return (
                                <div className='more' key={index}>{request.products.length} mÃ¡s</div>
                            )
                        }
                    })
                }
            </div>
            <div className='footer'>
                <div className='info'>Hace {request.minutes}</div>
                <div className='printer' onClick={onPrintOrder}>
                    <img src={Printer} alt={'Imprimir'} />
                </div>
                <div className='eye'>
                    <Link to={`/request-product-detail/${request.id}`}><img src={Eye} alt={'Detalle'} /></Link>
                </div>
            </div>
        </div>
    )
}
export default Request