
import React, { useEffect, useState } from 'react';
import '../assets/styles/request-products-detail.css';
import { Link } from 'react-router-dom';
import { Fragment } from 'react';
import config from './../config';

import Close from '../assets/images/cancel.svg';
import $ from 'jquery'
import axios from 'axios';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
toast.configure()


const RequestProductDetail = props => {

    const [id] = useState(props.match.params.id)
    const [request, setRequest] = useState({})
    const [products, setProducts] = useState([])

    $("input").on("click", function () {
        $(this).select();
    })

    useEffect(() => {
        axios.post(`${config.host}api/obtenerProductoPedido`, {
            id: props.match.params.id,
        }).then(function (response) {
            console.log('response.data', response.data)
            setProducts(response.data.products)
            setRequest(response.data.request)
        }).catch(function (error) {
            console.log(error);
        });

    }, [props])

    const onShowMenu = product => _ => {
        if (product.status === 'pending') {
            let newProducts = products.map(currentProduct => {
                if (currentProduct.id === product.id)
                    currentProduct.showMenu = true
                else
                    currentProduct.showMenu = false
                return currentProduct
            })
            setProducts(newProducts)
        }
    }
    const onHideMenu = product => _ => {
        let newProducts = products.map(currentProduct => {
            if (currentProduct.id === product.id) {
                currentProduct.showMenu = false
            }
            return currentProduct
        })
        setProducts(newProducts)
    }
    const onChangeQuantity = product => e => {
        let newProducts = products.map(currentProduct => {
            if (Number(e.target.value) <= product.cantidad && Number(e.target.value) > 0) {
                if (currentProduct.id === product.id) {
                    currentProduct.quantityWrite = Number(e.target.value)
                    currentProduct.error = false
                }
            } else {
                currentProduct.error = true
            }
            return currentProduct
        })
        setProducts(newProducts)
    }
    const onChangeStatusProduct = (product, status) => _ => {

        if (product.quantityWrite > 0 && !product.error) {
            axios.post(`${config.host}api/actualizarEstadoProductoPedido`, {
                id,
                product: product,
                status: status
            }).then(function (response) {
                if (response.data.status) {
                    let counterChecked = 0
                    let newProducts = products.map(currentProduct => {
                        if (currentProduct.status === 'check' || currentProduct.status === 'cancel')
                            counterChecked++;
                        if (currentProduct.id === product.id) {
                            counterChecked++;
                            currentProduct.status = status
                            currentProduct.showMenu = false
                        }
                        return currentProduct
                    })
                    setProducts([...products, newProducts])
                    if (counterChecked === products.length)
                        finishRequest()
                } else {
                    toast.error(response.data.message)
                }
            }).catch(function (error) {
                console.log(error);
            });
        } else {
            toast.dark('Cantidad no permitida')
        }
    }
    const finishRequest = () => {
        axios.post(`${config.host}api/finalizarPedido`, {
            id,
            status: 'finished'
        }).then(function (response) {
            if (response.data.status)
                props.history.push('/request-product');
            else
                toast.error(response.data.message)
        }).catch(function (error) {
            console.log(error);
        })
    }

    return (
        <Fragment>
            <div className='back'>
                <Link to='/request-product'>Atras</Link>
            </div>
            <div className='header'>
                <h1>{request ? request.para : ''}</h1>
            </div>
            <div className='body'>
                <table>
                    <thead>
                        <tr>
                            <th>PRODUCTO</th>
                            <th>CANTIDAD</th>
                            <th>ACCIÃN</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            products.map((product, index) => {
                                return (
                                    <tr key={index}>
                                        <td className={product.status}>{product.nombre}</td>
                                        <td className={product.status}>{product.cantidad}</td>
                                        <td className={product.status}>
                                            <div onClick={onShowMenu(product)}>{product.nombre}</div>
                                            <div className='menu' style={{ display: product.showMenu ? 'block' : 'none' }}>
                                                <div className='header'>
                                                    <div className='name'>{product.nombre}</div>
                                                    <div className='close' onClick={onHideMenu(product)}>
                                                        <img src={Close} alt={'Cerrar'} />
                                                    </div>
                                                </div>
                                                <div className='body'>
                                                    <input type='number' onChange={onChangeQuantity(product)} ></input>
                                                </div>
                                                <div className='footer'>
                                                    <button onClick={onChangeStatusProduct(product, 'check')}>Enviar</button>
                                                    <button className='negative' onClick={onChangeStatusProduct(product, 'cancel')}>Sin existencias</button>
                                                    {
                                                        product.error ?
                                                            <div className='error'>Cantidad no permitida</div>
                                                            : ''
                                                    }
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </div>

        </Fragment>)

}

export default RequestProductDetail